<template>
  <v-container
          class="fill-height"
          fluid
  >
    <v-row no-gutters justify="center">
      <v-col cols="12" md="10">
        <v-card class="pa-1">
          <h1>
            : <span :class="{ alert: status < 0 }">{{deviceStatus}}</span>
          </h1>
        </v-card>
      </v-col>
    </v-row>
    <v-row
            align="center"
            justify="center"
            no-gutters
    >
      <v-col
              cols="12"
              sm="10"
              md="10"
      >
        <v-card class="elevation-12">
          <v-toolbar
                  color="primary"
                  dark
                  flat
                  dense
          >
            <v-toolbar-title></v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-data-table
                    :headers="category_headers"
                    :items="transactionItems"
                    :single-expand="false"
                    :expanded="transactionItems"
                    item-key="category"
                    show-expand
                    disable-pagination
                    hide-default-footer
                    height="600"
            >
              <template v-slot:expanded-item="{ headers, item }">
                <td :colspan="headers.length" style="padding: 8px;">
                  <v-data-table
                          v-model="item.selectedItems"
                          :headers="item_headers"
                          :items="item.items"
                          :single-select="false"
                          item-key="name"
                          show-select
                          disable-pagination
                          hide-default-footer
                  >
                    <template v-slot:item.count="props">
                      <v-edit-dialog :return-value.sync="props.item.count">
                        {{ props.item.count }}
                        <template v-slot:input>
                          <v-text-field
                            v-model="props.item.count"
                            type="number"
                            min="1"
                            single-line
                            ></v-text-field>
                        </template>
                      </v-edit-dialog>
                    </template>
                  </v-data-table>
                </td>
              </template>
            </v-data-table>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
                    color="primary"
                    @click="StartProcess"
                    :disabled="btnDisabled"
            ></v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    name: 'SelectItem',
    data: () => ({
      status: 0,
      category_headers: [
        {
          text: '',
          sortable: false,
          value: 'category'
        }
      ],
      item_headers: [
        {
          text: '',
          align: 'end',
          sortable: false,
          value: 'count',
          width: '150'
        },
        {
          text: '',
          align: 'start',
          sortable: false,
          value: 'name'
        }
      ],
      transactionItems: [
        {
          category: '',
          items: [
            {
              name: 'Richart',
              count: 1
            },
            {
              name: 'Richart()',
              count: 1
            },
            {
              name: '',
              count: 1
            }
          ],
          selectedItems: []
        },
        {
          category: '',
          items: [
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            },
            {
              name: '/',
              count: 1
            },
            {
              name: '/',
              count: 1
            },
            {
              name: '//',
              count: 1
            },
            {
              name: '//',
              count: 1
            },
            {
              name: '',
              count: 1
            }
          ],
          selectedItems: []
        },
        {
          category: '',
          items: [
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            }
          ],
          selectedItems: []
        },
        {
          category: '',
          items: [
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            },
            {
              name: '',
              count: 1
            }
          ],
          selectedItems: []
        },
        {
          category: '',
          items: [
            {
              name: '',
              count: 1
            }
          ],
          selectedItems: []
        }
      ]
    }),
    computed: {
      deviceStatus () {
        switch (this.status) {
          case -1:
            return ''
          case 0:
            return ''
          case 1:
            return ''
          case 2:
            return ''
        }
        return ''
      },
      btnDisabled () {
        if (this.status !== 2) return true
        var selected = []
        this.transactionItems.forEach(category => {
          category.selectedItems.forEach(item => {
            selected.push(item.name)
          })
        })
        return selected.length === 0
      }
    },
    methods: {
      StartProcess() {
        var selected = []
        this.transactionItems.forEach(category => {
          category.selectedItems.forEach(item => {
            selected.push(item.name)
          })
        })
        this.$root.transactions = selected
        this.$router.push('/unitOperation')
      }
    }
  }
</script>

<style scoped>
  .alert {
    color: red
  }
</style>
